/* MARGIN from top */
.main-menu>ul {
margin:7px 0;
}

.main-menu li {
position:relative;
display:block;
width:250px;
}

/* SIDEBAR content FONTS */
.main-menu li>a {
position:relative;
display:table;
border-collapse:collapse;
border-spacing:0;
font-family: arial;
font-size: 20px;
text-decoration:none;
-webkit-transform:translateZ(0) scale(1,1);
-webkit-transition:all .1s linear;
transition:all .1s linear;
}

.main-menu li>a#IBMSide {
    color:white;
}
.main-menu li>a#BROCCSide {
    color:black;
}
.main-menu li>a#ANALOGICSide {
    color:rgb(26, 108, 185);
}
.main-menu li>a#PS6Side {
    color:black;
}

.main-menu .nav-text {
position:relative;
display:table-cell;
vertical-align:middle;
width:190px;
  font-family: 'Titillium Web', sans-serif;
}

.no-touch .scrollable.hover {
overflow-y:hidden;
}

.no-touch .scrollable.hover:hover {
overflow-y:auto;
overflow:visible;
}

a:hover,a:focus {
text-decoration:none;
}

/* ICONS before expanding */
nav ul,nav li {
outline:0;
margin:0;
padding:0;
}

/* HIGHLIGHTS */
.main-menu li:hover>a,nav.main-menu li.active>a,.dropdown-menu>li>a:hover,.dropdown-menu>li>a:focus,.dropdown-menu>.active>a,.dropdown-menu>.active>a:hover,.dropdown-menu>.active>a:focus,.no-touch .dashboard-page nav.dashboard-menu ul li:hover a,.dashboard-page nav.dashboard-menu ul li.active a {
color:#fff;
background-color:#5fa2db;
}

